import React from 'react'
import Simulation from './Simulation.jsx';
import Story from './Story.jsx';

class Game extends React.Component{
    render(){
        console.log("rendering simulation");
        return <div>
                <p>Game</p>
                <Simulation />
                <Story />
            </div>
    }
}

export default Game



